<template>
<el-container>
    <el-header>
      <div class="name">{{top}}</div>
      <div class="top"><Top></Top></div>
      <el-dropdown @command="handleCommand">
            <span class="el-dropdown-link" v-if="!isAdmin">
                 欢迎你,{{$store.state.userinfo.employeeName}} <i class="el-icon-view"></i>
            </span>
            <span class="el-dropdown-link" v-else>
                 欢迎你,{{$store.state.admininfo.adminName}} <i class="el-icon-view"></i>
            </span>
            <el-dropdown-menu slot="dropdown" >
                <el-dropdown-item command="a">个人信息</el-dropdown-item>
                <el-dropdown-item command="b">注销</el-dropdown-item>
            </el-dropdown-menu>
        </el-dropdown>
    </el-header>
    <el-container>
        <el-aside width="200px"><Aside></Aside></el-aside>
        <el-main><router-view></router-view></el-main>
    </el-container>
</el-container>
</template>
<script>
export default {
  data () {
    return {
      top: 'welcome',
      isAdmin:this.$store.state.isAdmin
    }
  },
  components: {
    Aside: () => import('@/components/task-aside'),
    Top: () => import('@/components/task-header')
  },
  methods: {
    handleCommand (command) {
      if (command == 'a') {
        this.$router.push('/home/user')
      } else {
       localStorage.clear()
        this.$router.push('/')
      }
    }
  }
}
</script>

<style>
.el-dropdown {
    display: block;
    float: right;
    margin-right:30px;
    width: 200px;
}
body{
  background-color:aliceblue !important;
}
.name{
  display: block;
  float: left;
  font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;

}
  .top{
    display: block;
    float: left;
    margin-left: 45px;
  }
  .el-header{
    background-color: rgb(70, 180, 147) ;
    color: #333;
    text-align: center;
    line-height: 60px;
    height: 100%;
    margin: 0;
    padding: 0;
  }
  .el-container{
      height:100%;
      margin: 0;
      padding: 0;
  }
  .el-aside {
    background-color:steelblue;
    text-align: center;
    line-height: 200px;
    width: 106px !important;
    height: 43vw;
    padding: 0;
  }
  .el-main {
    background-color: #E9EEF3;
    color: #333;
    text-align: center;
    height: 100%!important;
    padding: 0;

  }
  span{
    color: #E9EEF3;
  }
</style>



